import axios from 'axios';
import HttpClient from "./http-client";
import { GET_INIT_PRODUCT, SIGN_UP_VERIFICATION, CREATE_WALL, EXTRA_DATA_UPDATE, BASE_URL } from './api'
import { USER_TOKEN, USER_AUTHENTICATED } from '../reducer/keys';
import Swal from 'sweetalert2';
import { TOAST_ERROR } from '../utils';


export const getInItProduct = () => {
    return new HttpClient().get(GET_INIT_PRODUCT)
}

