export const createWall = (shopOnBio = null) => {
    const url = shopOnBio ? `${CREATE_WALL}?slug=${shopOnBio}` : CREATE_WALL
    return new HttpClient().get(url)
}

export const signUpVerified = (token, history, callback) => {
    return (dispatch) => {
        new HttpClient().post(`${SIGN_UP_VERIFICATION}/${token}`).then((response) => {
            const { responseData, response_code, response_message, user } = response.data

            if (responseData?.promotor_key) {
                Object.keys(responseData.promotor_key).forEach(key => {
                    document.cookie = `${key}=${responseData.promotor_key[key]};`;
                });
            }

            if (responseData.user && Object.keys(responseData.user).length > 0) {
                callback(responseData.user.id);
            }

            if (response_code == 200) {
                localStorage.setItem('steps', '1');
                dispatch({ type: USER_TOKEN, payload: responseData });
                localStorage.setItem('token', responseData.token);
                if (responseData?.user?.emailId) localStorage.setItem('emailId', responseData?.user?.emailId);
                history('/accounts/price')
            }
            else if (response_code == 201) {
                Swal.fire({
                    title: response_message,
                    icon: "success",
                    showCancelButton: false,
                    showConfirmButton: true,
                    confirmButtonText: 'Go to Home',
                    confirmButtonColor: "#5354fc",
                }).then(result => {
                    localStorage.removeItem('steps')
                    window.location = BASE_URL
                })
            }
        })
    }
}