import React, { Component } from "react";
import { connect } from "react-redux";
import { signUpVerified } from '../../actions/initActions'

const channel = new BroadcastChannel('connect');
class VerifyEmail extends Component {
    constructor(props) { super(props) }
    componentWillMount() {
        const { params, history } = this.props;
        this.props.signUpVerified(params.id, history, this.sendMessage)
    }

    sendMessage = (userId) => {
        channel.postMessage(JSON.stringify({ id: userId, plan: "" }))
    }

    render() { return <></> }
}

const mapStateToProps = (state) => { return {} }

const mapDispatchToProps = dispatch => {
    return { signUpVerified: (token, history, callback) => dispatch(signUpVerified(token, history, callback)), }
}

export default connect(mapStateToProps, mapDispatchToProps)(VerifyEmail);